body1{
  display: flex;
  padding: 0 10px;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  /* background-color: #fcf1ed; */
  background-image: url(https://images8.alphacoders.com/361/thumb-1920-361036.jpg);
}
::selection{
  color: #fff;
  background: #0D6EFD;
}
.wrapper{
  width: 715px;
  background: #fff;
  border-radius: 5px;
  box-shadow: 10px 10px 10px rgba(0,0,0,0.05);
}
.wrapper header{
  font-size: 22px;
  font-weight: 600;
  padding: 20px 30px;
  border-bottom: 1px solid #ccc;
}
.wrapper form{
  margin: 35px 30px;
}
.wrapper form.disabled{
  pointer-events: none;
  opacity: 0.7;
}
form .dbl-field{
  display: flex;
  margin-bottom: 25px;
  justify-content: space-between;
}
.dbl-field .field{
  height: 50px;
  display: flex;
  position: relative;
  width: calc(100% / 2 - 13px);
}
.wrapper form i{
  position: absolute;
  top: 50%;
  left: 18px;
  color: #ccc;
  font-size: 17px;
  pointer-events: none;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
form .field input,
form .message textarea{
  width: 90%;
  height: 100%;
  outline: none;
  padding: 0 18px 0 48px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ccc;
}
.field input::-webkit-input-placeholder, .message textarea::-webkit-input-placeholder{
  color: #ccc;
}
.field input:-ms-input-placeholder, .message textarea:-ms-input-placeholder{
  color: #ccc;
}
.field input::placeholder,
.message textarea::placeholder{
  color: #ccc;
}
.field input:focus,
.message textarea:focus{
  padding-left: 47px;
  border: 2px solid #0D6EFD;
}
.field input:focus ~ i,
.message textarea:focus ~ i{
  color: #0D6EFD;
}
form .message{
  position: relative;
}
form .message i{
  top: 30px;
  font-size: 20px;
}
form .message textarea{
  min-height: 130px;
  max-height: 230px;
  max-width: 200%;
  min-width: 0%;
  padding: 15px 20px 0 48px;
}
form .message textarea::-webkit-scrollbar{
  width: 0px;
}
.message textarea:focus{
  padding-top: 14px;
}
form .button-area{
  margin: 25px 0;
  display: flex;
  align-items: center;
}
.button-area button{
  color: #fff;
  border: none;
  outline: none;
  font-size: 18px;
  cursor: pointer;
  border-radius: 5px;
  padding: 13px 25px;
  background: #0D6EFD;
  transition: background 0.3s ease;
}
.button-area button:hover{
  background: #025ce3;
}
.button-area span{
  font-size: 17px;
  margin-left: 30px;
  display: none;
}
@media (max-width: 600px){
  .wrapper header{
    text-align: center;
  }
  .wrapper form{
    margin: 35px 20px;
  }
  form .dbl-field{
    flex-direction: column;
    margin-bottom: 0px;
  }
  form .dbl-field .field{
    width: 100%;
    height: 45px;
    margin-bottom: 20px;
  }
  form .message textarea{
    resize: none;
  }
  form .button-area{
    margin-top: 20px;
    flex-direction: column;
  }
  .button-area button{
    width: 100%;
    padding: 11px 0;
    font-size: 16px;
  }
  .button-area span{
    margin: 20px 0 0;
    text-align: center;
  }
}
.privacy-page-top {
    color: #888888;
    font-family: 'Cabin', sans-serif;
    font-size: 1.5em;
    margin-top: 5%;
    font-weight: bold;
    margin-left: 12%;
}

.privacy-page-body {
    display: inline-block;
    margin-left: 12%;
    margin-right: 5%;
}

.privacy-page-title {
    font-family: "Roboto", sans-serif;
    color: #2781bc;
    font-size: 4.5em;
    margin-bottom: 2%;
    font-weight: lighter;
    display: inline-block;
    width: 70%;
}

.privacy-paragraph-heading {
    font-family: "Abhaya Libre", sans-serif;
    color: black;
    font-weight: bolder;
    font-size: 1.5em;
    margin-bottom: 3%;
}

.privacy-content {
    font-family: 'Abhaya Libre', sans-serif;
    margin-bottom: 8%;
    display: inline-block;
    width: 70%;
}

.privacy-paragraph-body {
    margin-right: 4%;
    margin-bottom: 4em;
}

.privacy-paragraph-text-followed {
    margin-bottom: 1em;
    color: #717171;
}

.privacy-paragraph-text {
    color: #717171;
}

.privacy-paragraph-body {
    line-height: 2em;
    font-family: 'Cabin', sans-serif;
}

.privacy-policy-nav-menu {
    width: 27%;
    display: inline-block;
    position: relative;
    vertical-align: top;
}

.privacy-nav-menu-wrapper {
    background-color: #f39c12;
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: block;
}

.privacy-nav-menu-wrapper a {
    display: block;
    height: inherit;
    width: inherit;
    padding-left: 1em;
    padding-bottom: 1em;
    padding-top: 1em;
}

.privacy-nav-menu2,
.privacy-nav-menu1 {
    display: block;
    background-color: #f39c12;
}

.site-map-link:hover,
.privacy-policy-link,
.privacy-policy-link:hover {
    color: white;
    text-decoration: none;
}

.privacy-nav-menu2 {
    background-color: white;
    border-bottom-width: 2px;
    border-bottom-style: groove;
    border-bottom-color: #f6f6f6;
}

.site-map-link {
    color: #f39c12;
    text-decoration: none;
}

.privacy-nav-menu2:hover {
    background-color: #f39c12;
}

